export const rules = {
    login_required: value => !!value || '   ...',
    login_counter: value => value.length >= 3 || '   3 ',
    login_match: value => {
        const pattern = /^[0-9a-zA-Z]+$/
        return pattern.test(value) || '   3 ,     ...'
    },
    pass_required: value => !!value || '   ...',
    pass_counter: value => value.length >= 5 || '   5 ',
    pass_match: value => {
        const pattern = /^[0-9a-zA-Z{},.<>;:$*!?^]+$/
        return pattern.test(value) || '   5 , ,     ...'
    }
}